import '../css/special.styl';

import BaseSpecial from './base';
import { makeElement, removeChildren } from './lib/dom';
import * as Share from './lib/share';
import * as Analytics from './lib/analytics';
import Data from './data';
import Svg from './svg';

const CSS = {
  main: 'vhs',
};

const EL = {};

const IMAGES = [];

class Special extends BaseSpecial {
  constructor(params = {}) {
    super();

    Object.assign(this.params, params);
    this.saveParams();

    if (Data && params.data) {
      Object.assign(Data, params.data);
    }

    if (this.params.css) {
      this.loadStyles(this.params.css).then(() => this.init());
    } else {
      this.init();
    }
  }

  static loadImages() {
    Object.entries(Data.movies).forEach(([key, value]) => {
      const tape = document.createElement('img');
      tape.src = value.tape;
      IMAGES.push(tape);

      const cover = document.createElement('img');
      cover.src = value.cover;
      IMAGES.push(cover);

      if (value.bg.correct) {
        const correct = document.createElement('img');
        correct.src = value.bg.correct;
        IMAGES.push(correct);
      }

      if (value.bg.incorrect) {
        const incorrect = document.createElement('img');
        incorrect.src = value.bg.incorrect;
        IMAGES.push(incorrect);
      }
    });
  }

  static createElements() {
    EL.main = makeElement('div', CSS.main);
    EL.mBg = makeElement('div', `${CSS.main}__bg`);
    EL.mInner = makeElement('div', `${CSS.main}__inner`);

    EL.main.appendChild(EL.mBg);
    EL.main.appendChild(EL.mInner);

    EL.enter = makeElement('div', `${CSS.main}-enter`);
    EL.eLogo = makeElement('img', `${CSS.main}-enter__logo`, {
      src: 'https://leonardo.osnova.io/4b5e6c9a-06b0-58f6-131e-0767b9e2c566/',
    });
    EL.eText = makeElement('div', `${CSS.main}-enter__text`, {
      textContent: Data.description,
    });
    EL.eBtn = makeElement('button', `${CSS.main}-enter__btn`, {
      innerHTML: `<span>Play</span>${Svg.arrow}`,
      data: {
        click: 'start',
      },
    });

    EL.enter.appendChild(EL.eLogo);
    EL.enter.appendChild(EL.eText);
    EL.enter.appendChild(EL.eBtn);

    EL.test = makeElement('div', `${CSS.main}-test`);
    EL.tLogo = makeElement('img', `${CSS.main}-test__logo`, {
      src: 'https://leonardo.osnova.io/4b5e6c9a-06b0-58f6-131e-0767b9e2c566/',
    });
    EL.tFrame = makeElement('img', `${CSS.main}-test__frame`, {
      src: 'https://leonardo.osnova.io/29c5f5ae-ab14-b6c3-968e-931d8619b008/',
    });
    EL.tInner = makeElement('div', `${CSS.main}-test__inner`);

    EL.test.appendChild(EL.tLogo);
    EL.test.appendChild(EL.tFrame);
    EL.test.appendChild(EL.tInner);

    EL.q = makeElement('div', `${CSS.main}-q`);
    EL.qTitle = makeElement('div', `${CSS.main}-q__title`);
    EL.qText = makeElement('div', `${CSS.main}-q__text`);
    EL.qOptions = makeElement('div', `${CSS.main}-q__options`);
    EL.options = makeElement('div', `${CSS.main}-options`);

    EL.qOptions.appendChild(EL.options);

    EL.q.appendChild(EL.qTitle);
    EL.q.appendChild(EL.qText);
    EL.q.appendChild(EL.qOptions);

    EL.a = makeElement('div', `${CSS.main}-a`);
    EL.aImg = makeElement('img', `${CSS.main}-a__img`);
    EL.aBody = makeElement('div', `${CSS.main}-a__body`);
    EL.aText = makeElement('div', `${CSS.main}-a__text`);
    EL.aBtn = makeElement('button', `${CSS.main}-a__btn`, {
      innerHTML: `<span>Продолжить</span>${Svg.arrow}`,
      data: {
        click: 'continue',
      },
    });

    EL.aBody.appendChild(EL.aText);
    EL.aBody.appendChild(EL.aBtn);

    EL.a.appendChild(EL.aImg);
    EL.a.appendChild(EL.aBody);

    EL.result = makeElement('div', `${CSS.main}-result`);
    EL.rImg = makeElement('img', `${CSS.main}-result__img`);
    EL.rInner = makeElement('div', `${CSS.main}-result__inner`);
    EL.rTitle = makeElement('div', `${CSS.main}-result__title`);
    EL.rResult = makeElement('div', `${CSS.main}-result__result`);
    EL.rDesc = makeElement('div', `${CSS.main}-result__description`);
    EL.rShare = makeElement('div', `${CSS.main}-result__share`);
    EL.rRestart = makeElement('div', `${CSS.main}-result__restart`, {
      innerHTML: `<span>Пройти еще раз</span>${Svg.refresh}`,
      data: {
        click: 'restart',
      },
    });

    EL.rInner.appendChild(EL.rTitle);
    EL.rInner.appendChild(EL.rResult);
    EL.rInner.appendChild(EL.rDesc);
    EL.rInner.appendChild(EL.rShare);
    EL.rInner.appendChild(EL.rRestart);

    EL.result.appendChild(EL.rImg);
    EL.result.appendChild(EL.rInner);

    EL.mInner.appendChild(EL.enter);
  }

  makeOptions(options) {
    removeChildren(EL.options);

    options.forEach((item, i) => {
      const optionWrap = makeElement('div', `${CSS.main}-options__item`);
      const option = makeElement('div', `${CSS.main}-option`, {
        data: {
          index: i,
          click: 'answer',
        },
      });
      const tape = Data.movies[item.id].tape;
      option.style.backgroundImage = `url(${tape})`;
      optionWrap.appendChild(option);

      EL.options.appendChild(optionWrap);
    });
  }

  static getResult(score) {
    let result = '';
    Data.results.some((item) => {
      if (item.range[0] <= score && item.range[1] >= score) {
        result = item;
        return true;
      }

      return false;
    });

    return result;
  }

  start() {
    Analytics.sendEvent('Start');

    EL.mInner.replaceChild(EL.test, EL.enter);

    this.makeNextQuestion();
  }

  restart() {
    Analytics.sendEvent('Restart');

    this.setInitialParams();

    EL.main.classList.remove('is-result');

    EL.aBtn.innerHTML = `<span>Продолжить</span>${Svg.arrow}`;
    EL.aBtn.dataset.click = 'continue';

    this.makeNextQuestion();
  }

  continue() {
    Analytics.sendEvent(`Next - ${this.activeIndex + 1}`);

    this.activeIndex += 1;

    this.makeNextQuestion();
  }

  makeNextQuestion() {
    const q = Data.questions[this.activeIndex];

    EL.main.classList.remove('is-answered');

    removeChildren(EL.tInner);

    EL.qTitle.textContent = `Вопрос #${this.activeIndex + 1}`;
    EL.qText.textContent = q.text;

    this.makeOptions(q.options);

    EL.tInner.appendChild(EL.q);
  }

  answer(el) {
    Analytics.sendEvent(`Option - ${this.activeIndex + 1}`);

    const index = el.dataset.index;
    const q = Data.questions[this.activeIndex];
    const option = q.options[index];
    const movie = Data.movies[option.id];

    EL.main.classList.add('is-answered');

    removeChildren(EL.tInner);
    EL.tInner.appendChild(EL.a);

    EL.aImg.src = movie.cover;
    EL.aText.innerHTML = option.answer;

    if (q.options[index].isCorrect) {
      this.correctAnswers += 1;

      EL.a.style.backgroundImage = `url(${movie.bg.correct})`;
    } else {
      EL.a.style.backgroundImage = `url(${movie.bg.incorrect})`;
    }

    if (this.activeIndex === Data.questions.length - 1) {
      EL.aBtn.innerHTML = `<span>Результат</span>${Svg.arrow}`;
      EL.aBtn.dataset.click = 'result';
    }
  }

  result() {
    Analytics.sendEvent('Result');

    EL.main.classList.remove('is-answered');
    EL.main.classList.add('is-result');

    const result = Special.getResult(this.correctAnswers);

    removeChildren(EL.tInner);
    EL.tInner.appendChild(EL.result);

    EL.rImg.src = result.img;
    EL.rTitle.textContent = result.title;
    EL.rResult.textContent = `${this.correctAnswers} из ${Data.questions.length} правильных ответов:`;
    EL.rDesc.textContent = result.description;

    removeChildren(EL.rShare);
    Share.make(EL.rShare, {
      url: `${this.params.share.url}/${this.correctAnswers}`,
      title: this.params.share.title,
      twitter: this.params.share.title,
    });
  }

  setInitialParams() {
    this.activeIndex = 0;
    this.correctAnswers = 0;
  }

  init() {
    this.setInitialParams();

    if (this.params.isFeed) {
      this.container.classList.add('is-feed');
    }

    Special.loadImages();
    Special.createElements();

    this.container.appendChild(EL.main);

    Analytics.sendEvent('First screen', 'Show');
  }
}

export default Special;
